	module.exports = {"data":{"markdownRemark":{"html":"<p>You can use radio buttons for questions where you want the user to only give you one answer.</p>\n<p>Radio buttons are a type of input. They should all be nested in their own label element. Furthermore, all related radio buttons should have the same name attribute.</p>\n<p>Here’s an example of a radio button:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code>    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>label</span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>input</span> <span class=\"token attr-name\">type</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>radio<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">name</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>indoor-outdoor<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span> Indoor<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>label</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>","fields":{"slug":"/certificates/create-a-set-of-radio-buttons/"},"frontmatter":{"title":"Create a Set of Radio Buttons"}}},"pathContext":{"slug":"/certificates/create-a-set-of-radio-buttons/"}}

